.list {

  width: 1200px;
  max-width: 95%;
  margin: 2rem auto 2rem auto;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
  padding: 1rem;
}

.list ul {
  list-style: none;

}

.list li {
  padding: .5rem;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}

.list li:not(:last-of-type) {
  margin-bottom: 1rem;
}

.list li a {
  color: #1a1a1a;
  cursor: pointer;

}



.user img {
  width: 150px;
  height: 150px;
}

.user {
  margin-bottom: 1rem;

}

.user a {
  text-decoration: none;
}

.user a:hover {
  text-decoration: underline;
}

.list-items {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: .5rem;
}

@media (max-width:768px) {
  .list-items {
    grid-template-columns: 1fr;
  }
}

.list span {
  font-weight: bold;
}

.list p {
  font-size: 1.2rem;
}

.repo {
  margin-bottom: .5rem;
}